.program__top-block-wrapper {
  background: var(--main-color);
  padding-top: 24px;
  padding-bottom: 20px;
}

.program__bottom-block-container {
  background: var(--main-color);
  padding-bottom: 106px;
}

@media (max-width: 768px) {
  .program__bottom-block-container {
    padding-bottom: 0;
    margin-bottom: 10px;
  }
}

.program__bottom-blocks {
  padding: 18px 0 64px 0;
  background-color: #fff;
  border-radius: 0 25px 25px 25px;
}

@media (max-width: 768px) {
  .program__bottom-blocks {
    border-radius: 0 25px 0 0;
  }
}

.program__bottom-block-wrapper {
  display: none;
}

.program__bottom-block-wrapper.mod-show {
  display: block;
}

.program__head {
  display: flex;
  justify-content: space-between;
  margin-bottom: 66px;
}

@media (max-width: 768px) {
  .program__head {
    display: block;
    margin-bottom: 26px;
  }
}

.program__title {
  margin-bottom: 0;
}

.program__navigate-wrapper {
  margin: 30px -10px -10px 20px;
}

@media (max-width: 768px) {
  .program__navigate-wrapper {
    margin-left: 0;
    margin-top: 54px;
  }
}

.program__navigate-radio-wrapper {
  display: inline-block;
  margin-right: 10px;
  margin-bottom: 10px;
}

.program__navigate-radio {
  display: none;
}

.program__navigate-radio:checked + .program__navigate-radio-content {
  background-color: #302463;
  border-color: transparent;
}

@media (min-width: 1024px) {
  .program__navigate-radio-content:hover {
    border-color: transparent;
    background-color: #6b59b6;
  }
}

.program__navigate-radio-content {
  font-weight: 600;
  font-size: 22px;
  white-space: nowrap;
  padding: 16px 28px;
  border-radius: 0 35px 35px 35px;
  border: 2px solid #fff;
  color: #fff;
  background-color: transparent;
  height: min-content;
  transition: background-color 350ms, border-color 350ms;
  display: block;
  cursor: pointer;
}

@media (max-width: 1023px) {
  .program__navigate-radio-content {
    font-size: 14px;
    padding: 10px 16px;
  }
}

.program__navigate-radio-content:last-child {
  margin-right: 0;
}

.program__filters-wrapper-caption {
  display: none;
}

@media (max-width: 768px) {
  .program__filters-wrapper-caption {
    display: block;
    font-size: 14px;
    color: rgba(229, 229, 229, 0.2);
    margin-bottom: 18px;
  }
}

.program__filters-wrapper {
  margin-bottom: -14px;
  margin-right: -14px;
}

.program__filter-checkbox-wrapper {
  margin-bottom: 14px;
  margin-right: 14px;
  display: inline-block;
}

.program__filter-checkbox {
  display: none;
}

.program__filter-checkbox:checked + .program__filter-checkbox-content {
  background-color: #302463;
  color: #fff;
  background-image: url('../../img/old/img/filter-minus.svg');
}

@media (min-width: 1024px) {
  .program__filter-checkbox-content:hover {
    background-color: #6b59b6;
    color: #fff;
    background-image: url('../../img/old/img/filter-minus.svg');
  }
}

.program__filter-checkbox-content {
  font-weight: 600;
  font-size: 14px;
  white-space: nowrap;
  background-size: 18px 18px;
  transition: background-color 350ms, color 350ms, background-image 100ms;
  background-image: url('../../img/old/img/filter-plus.svg');
  background-repeat: no-repeat;
  background-position: top 50% right 20px;
  padding: 20px 80px 20px 20px;
  display: block;
  background-color: #fff;
  border-radius: 28px 28px 28px 0;
  cursor: pointer;
}

@media (max-width: 768px) {
  .program__filter-checkbox-content {
    padding: 10px 46px 10px 20px;
    background-size: 16px 16px;
  }
}

.program__content-wrapper {

  margin: 0;
}

@media (max-width: 768px) {
  .program__content-wrapper {
    border-radius: 0 25px 0 0;
  }
}

.program__padding-content {
  padding-left: 62px;
  padding-right: 62px;
}

@media (max-width: 1023px) {
  .program__padding-content {
    padding-left: 12px;
    padding-right: 12px;
  }
}

.program__content-item {
  grid-template-columns: 1fr 8fr 5fr;
  grid-gap: 40px;
  border-bottom: 1px solid var(--main-color);
  display: grid;
  padding-top: 40px;
  padding-bottom: 40px;

}

.program__content-item.mod-hide {
  display: none;
}

@media (max-width: 1023px) {
  .program__content-item {
    grid-gap: 20px;
  }
}

@media (max-width: 768px) {
  .program__content-item {
    grid-template-columns: 50px 1fr;
    grid-gap: 40px 10px;
    padding-top: 48px;
    padding-bottom: 48px;
  }
}

@media (max-width: 567px) {
  .program__content-item {
    grid-template-columns: 26px 1fr;
  }
}

.program__content-item.last-of-type {
  border-bottom: none;
}

.program__content-item-time-wrapper {
  width: 100%;
  height: 100%;
}

@media (max-width: 768px) {
  .program__content-item-time-wrapper {
    grid-row: 1 / 3;
  }
}

.program__content-item-time {
  font-weight: bold;
  font-size: 24px;
  color: var(--secondary-color);
  transform: translate(-20%, 50%) rotate(270deg);
  white-space: nowrap;
  text-align: center;
}

@media (max-width: 768px) {
  .program__content-item-time {
    transform: translate(-20%, 100%) rotate(270deg);
  }
}

.program__content-info-tile {
  margin-bottom: 34px;
  font-weight: 600;
  font-size: 26px;
  line-height: 34px;
  margin-top: -8px;
}

@media (max-width: 768px) {
  .program__content-info-tile {
    font-size: 18px;
    line-height: 22px;
    margin-bottom: 12px;
    margin-top: -6px;
  }
}

.program__content-info-list {
  max-height: 0;
  overflow: hidden;
  transition: max-height 300ms, margin-bottom 300ms;
}

.program__content-info-list.mod-show {
  max-height: 200px;
  margin-bottom: 23px;
}

.program__content-info-item {
  margin-bottom: 14px;
  font-size: 14px;
  line-height: 22px;
  padding-left: 28px;
  position: relative;
}


.program__content-info-item:before {
  position: absolute;
  content: '';
  top: 10px;
  left: 0;
  width: 10px;
  height: 2px;
  background-color: #000;
}

.program__content-info-item:last-child {
  margin-bottom: 0;
}

.program__content-info-btn-show {
  font-weight: 600;
  font-size: 14px;
  padding-bottom: 2px;
  border-bottom: 1px dashed transparent;
  transition: border-bottom 250ms;
  margin-bottom: 40px;
  color: var(--secondary-color);
}

.program__content-info-btn-show:hover {
  border-bottom-color: var(--secondary-color);
}

.program__content-info-marks-wrapper {
  display: flex;
  align-items: center;
}

@media (max-width: 768px) {
  .program__content-info-marks-wrapper {
    display: block;
  }
}

.program__content-info-geo {
  margin-right: 40px;
  font-size: 14px;
  padding-left: 28px;
  background-image: url('../../img/old/img/geolokation.svg');
  background-repeat: no-repeat;
  background-position: top 50% left;
  background-size: 18px 20px;
}

@media (max-width: 768px) {
  .program__content-info-geo {
    margin-right: 0;
    margin-bottom: 18px;
  }
}

.program__content-info-mark {
  font-weight: 600;
  font-size: 12px;
  color: #fff;
  padding: 4px 16px;
  background: #000;
  border-radius: 0 14px 14px 14px;
  display: inline;
}

.program__content-author-wrapper {
  display: flex;
}

.program__content-author-image-wrapper {
  width: 74px;
  height: 70px;
  margin-right: 32px;
  flex: 0 0 74px;

  &:before {
    position: absolute;
    content: '';
    width: 100%;
    height: 64px;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 28px 28px 28px 0;
    background-color: #000;
  }

  &:after {
    height: 56px;
    width: 56px;
  }
}

.program__content-author-image {
  position: absolute;
  height: 56px;
  width: 56px;
  object-fit: cover;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.program__content-author-caption {
  font-weight: 600;
  font-size: 20px;
  line-height: 26px;
  margin-bottom: 16px;
}

.program__content-author-value {
  font-size: 14px;
  line-height: 18px;
}

@media (max-width: 768px) {
  .program__site-width-none {
    padding: 0;
  }
}

.program__btns-wrapper {
  display: flex;
  margin-top: 28px;
}

@media (max-width: 426px) {
  .program__btns-wrapper {
   flex-direction: column;
  }
}

.program__btn-download {
  margin-left: 18px;
  padding-left: 74px;
  position: relative;

  &:hover {
    .program__btn-download-icon {
      fill: #fff;
    }
  }
}

.program__btn-download-icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 30px;
  fill: #000;
  transition: fill 300ms;
}

@media (max-width: 768px) {
  .program__btn-download {
    padding-left: 50px !important;
  }
}

@media (max-width: 426px) {
  .program__btn-download {
    margin-left: 0 !important;
    margin-top: 16px !important;
  }
}
